"use client";
import { useMemo } from "react";

export default function AlertaStock() {
  const productos = [
    { nombre: "Switch 8P", stock: 2, minimo: 5 },
    { nombre: "Router Mikrotik", stock: 10, minimo: 3 },
    { nombre: "Access Point", stock: 1, minimo: 4 },
    { nombre: "Cable UTP 30m", stock: 6, minimo: 6 },
  ];

  const productosEnAlerta = useMemo(() => productos.filter(p => p.stock < p.minimo), [productos]);

  const exportarCSV = () => {
    const filas = productosEnAlerta.map(p => `${p.nombre},${p.stock},${p.minimo}`).join("\n");
    const contenido = `Producto,Stock Actual,Stock Mínimo\n${filas}`;
    const blob = new Blob([contenido], { type: "text/csv" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "productos_bajo_stock.csv";
    a.click();
    URL.revokeObjectURL(url);
  };

  return (
    <section>
      <h2>Alerta de Bajo Stock</h2>

      {productosEnAlerta.length === 0 ? (
        <p className="badge-stock badge-verde">✅ Todo bien. No hay productos en alerta.</p>
      ) : (
        <>
          <button onClick={exportarCSV}>📥 Exportar CSV</button>
          <table>
            <thead>
              <tr>
                <th>Producto</th>
                <th>Stock Actual</th>
                <th>Stock Mínimo</th>
                <th>Estado</th>
              </tr>
            </thead>
            <tbody>
              {productosEnAlerta.map((p, i) => (
                <tr key={i}>
                  <td>{p.nombre}</td>
                  <td style={{ color: "red", fontWeight: "bold" }}>{p.stock}</td>
                  <td>{p.minimo}</td>
                  <td>
                    <span className={`badge-stock ${p.stock === 0 ? "badge-rojo" : "badge-amarilla"}`}>
                      {p.stock === 0 ? "Agotado" : "Bajo Stock"}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </>
      )}
    </section>
  );
}
