"use client";
import { useState } from "react";

export default function InventarioFisico() {
  const [producto, setProducto] = useState("");
  const [sistema, setSistema] = useState(0);
  const [fisico, setFisico] = useState(0);
  const [registros, setRegistros] = useState<
    { producto: string; sistema: number; fisico: number }[]
  >([]);
  const [filtro, setFiltro] = useState("");

  const agregarComparacion = () => {
    if (!producto || sistema < 0 || fisico < 0) {
      alert("Completa correctamente todos los campos.");
      return;
    }

    setRegistros(prev => [...prev, { producto, sistema, fisico }]);
    setProducto("");
    setSistema(0);
    setFisico(0);
  };

  const exportar = () => {
    const contenido = registros.map(r => `${r.producto},${r.sistema},${r.fisico}`).join("\n");
    const blob = new Blob([`Producto,Sistema,Físico\n${contenido}`], { type: "text/csv" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "inventario_fisico.csv";
    a.click();
    URL.revokeObjectURL(url);
  };

  const filtrados = registros.filter(r =>
    r.producto.toLowerCase().includes(filtro.toLowerCase())
  );

  return (
    <section>
      <h2>Inventario Físico vs Sistema</h2>
      <div className="grid">
        <div>
          <label>Producto</label>
          <input value={producto} onChange={e => setProducto(e.target.value)} />
        </div>
        <div>
          <label>Stock en Sistema</label>
          <input type="number" value={sistema} min={0} onChange={e => setSistema(Number(e.target.value))} />
        </div>
        <div>
          <label>Stock Físico</label>
          <input type="number" value={fisico} min={0} onChange={e => setFisico(Number(e.target.value))} />
        </div>
      </div>

      <button onClick={agregarComparacion}>Agregar Comparación</button>
      <button onClick={exportar} style={{ marginLeft: "10px" }}>📥 Exportar CSV</button>

      <input
        type="text"
        placeholder="Buscar producto..."
        value={filtro}
        onChange={e => setFiltro(e.target.value)}
        style={{ marginTop: "1rem", width: "100%" }}
      />

      {filtrados.length > 0 && (
        <table>
          <thead>
            <tr>
              <th>Producto</th>
              <th>Sistema</th>
              <th>Físico</th>
              <th>Diferencia</th>
            </tr>
          </thead>
          <tbody>
            {filtrados.map((r, i) => (
              <tr key={i}>
                <td>{r.producto}</td>
                <td>{r.sistema}</td>
                <td>{r.fisico}</td>
                <td style={{ color: r.fisico > r.sistema ? "green" : r.fisico < r.sistema ? "red" : "#555" }}>
                  {r.fisico - r.sistema}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </section>
  );
}
