"use client";

import { useState } from "react";
import EntradasSalidas from "../../components/almacen/EntradasSalidas";

import KardexHistorial from "../../components/almacen/KardexHistorial";
import InventarioFisico from "../../components/almacen/InventarioFisico";
import AlertaStock from "../../components/almacen/AlertaStock";
import '../../../styles/almacen.css'; 

export default function GestionAlmacenPage() {
  const [tab, setTab] = useState("entradas");

  return (
    <main className="p-6">
      <h1 className="text-2xl font-bold mb-4 text-center">Gestión de Almacén</h1>

      {/* Botones de pestañas */}
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-5 gap-2 mb-6">
        <button onClick={() => setTab("entradas")} className={tab === "entradas" ? "bg-blue-600 text-white px-4 py-2 rounded" : "bg-gray-200 px-4 py-2 rounded"}>
          Entradas/Salidas
        </button>
       
        <button onClick={() => setTab("kardex")} className={tab === "kardex" ? "bg-blue-600 text-white px-4 py-2 rounded" : "bg-gray-200 px-4 py-2 rounded"}>
          Kardex
        </button>
        <button onClick={() => setTab("fisico")} className={tab === "fisico" ? "bg-blue-600 text-white px-4 py-2 rounded" : "bg-gray-200 px-4 py-2 rounded"}>
          Inventario Físico
        </button>
        <button onClick={() => setTab("alertas")} className={tab === "alertas" ? "bg-blue-600 text-white px-4 py-2 rounded" : "bg-gray-200 px-4 py-2 rounded"}>
          Bajo Stock
        </button>
      </div>

      {/* Contenido de cada pestaña */}
      {tab === "entradas" && <EntradasSalidas />}

      {tab === "kardex" && <KardexHistorial />}
      {tab === "fisico" && <InventarioFisico />}
      {tab === "alertas" && <AlertaStock />}
    </main>
  );
}
