"use client";

import { useState } from "react";
import { Download, Plus } from "lucide-react";

interface Movimiento {
  producto: string;
  cantidad: number;
  tipo: "entrada" | "salida";
  fecha: string;
}

export default function EntradasSalidas() {
  const [producto, setProducto] = useState("");
  const [cantidad, setCantidad] = useState(0);
  const [tipoMovimiento, setTipoMovimiento] = useState<"entrada" | "salida">("entrada");
  const [fecha, setFecha] = useState("");
  const [historial, setHistorial] = useState<Movimiento[]>([]);
  const [filtro, setFiltro] = useState("");

  const handleGuardar = () => {
    if (!producto || cantidad <= 0 || !fecha) {
      alert("Completa todos los campos correctamente.");
      return;
    }

    const nuevoMovimiento: Movimiento = { producto, cantidad, tipo: tipoMovimiento, fecha };
    setHistorial((prev) => [...prev, nuevoMovimiento]);

    // Reset inputs
    setProducto("");
    setCantidad(0);
    setTipoMovimiento("entrada");
    setFecha("");
  };

  const exportarCSV = () => {
    const encabezado = "Producto,Cantidad,Tipo,Fecha";
    const filas = historial.map(
      (m) => `${m.producto},${m.cantidad},${m.tipo},${m.fecha}`
    );
    const contenido = [encabezado, ...filas].join("\n");
    const blob = new Blob([contenido], { type: "text/csv" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "movimientos_almacen.csv";
    a.click();
    URL.revokeObjectURL(url);
  };

  const movimientosFiltrados = historial.filter((m) =>
    m.producto.toLowerCase().includes(filtro.toLowerCase())
  );

  return (
    <section>
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-xl font-semibold">📦 Registro de Entradas y Salidas</h2>
        {historial.length > 0 && (
          <button onClick={exportarCSV} className="flex items-center gap-2">
            <Download size={16} /> Exportar CSV
          </button>
        )}
      </div>

      <div className="grid gap-4 grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 mb-6">
        <div>
          <label className="filtro-label">Producto</label>
          <input
            type="text"
            value={producto}
            onChange={(e) => setProducto(e.target.value)}
            placeholder="Ej: Access Point"
          />
        </div>

        <div>
          <label className="filtro-label">Cantidad</label>
          <input
            type="number"
            value={cantidad}
            onChange={(e) => setCantidad(Number(e.target.value))}
            min={1}
          />
        </div>

        <div>
          <label className="filtro-label">Tipo</label>
          <select
            value={tipoMovimiento}
            onChange={(e) => setTipoMovimiento(e.target.value as "entrada" | "salida")}
          >
            <option value="entrada">Entrada</option>
            <option value="salida">Salida</option>
          </select>
        </div>

        <div>
          <label className="filtro-label">Fecha</label>
          <input type="date" value={fecha} onChange={(e) => setFecha(e.target.value)} />
        </div>
      </div>

      <button onClick={handleGuardar} className="flex items-center gap-2 mb-6">
        <Plus size={16} /> Guardar Movimiento
      </button>

      {historial.length > 0 && (
        <>
          <input
            type="text"
            value={filtro}
            onChange={(e) => setFiltro(e.target.value)}
            placeholder="🔍 Buscar producto..."
            className="mb-4 w-full"
          />

          <div className="overflow-x-auto">
            <table>
              <thead>
                <tr>
                  <th>Producto</th>
                  <th>Cantidad</th>
                  <th>Tipo</th>
                  <th>Fecha</th>
                </tr>
              </thead>
              <tbody>
                {movimientosFiltrados.map((m, i) => (
                  <tr key={i}>
                    <td>{m.producto}</td>
                    <td>{m.cantidad}</td>
                    <td>
                      <span className={`badge-stock ${m.tipo === "entrada" ? "badge-verde" : "badge-rojo"}`}>
                        {m.tipo === "entrada" ? "Entrada" : "Salida"}
                      </span>
                    </td>
                    <td>{m.fecha}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </>
      )}
    </section>
  );
}
